import {
    Component,
    Prop,
    h,
    State,
    Listen,
    Method,
    Element,
    Event,
    Host,
    EventEmitter,
} from '@stencil/core';

export type SidePanelDirection = 'left' | 'right';
export type SidePanelSize = 'xs' | 'sm' | 'md' | 'lg' | 'xl';
export type SidePanelPosition = 'absolute' | 'fixed';

/**
 * @category Overlays
 * @description A sliding side panel component for displaying secondary content or forms. Features customizable positioning, backdrop, and animation options.
 *
 * @slot - Display content within the dialog
 *
 * @dependency at-button
 */
@Component({
    tag: 'at-side-panel',
    styleUrl: 'at-side-panel.scss',
    scoped: true,
})
export class AtSidePanelComponent {
    @Element() el!: HTMLAtSidePanelElement;

    /**
     * Size of the size panel
     */
    @Prop({ reflect: true })
    size: SidePanelSize = 'xs';

    /**
     * Title displayed in the side panel
     */
    @Prop()
    panel_title: string;

    /**
     * Subtitle displayed in the side panel
     */
    @Prop()
    panel_subtitle: string;

    /**
     *  Position of the side panel
     */
    @Prop({ reflect: true })
    origin: SidePanelDirection = 'right';

    /**
     * Enables scroll overflow on the sidepanel container
     */
    @Prop({ reflect: true })
    has_scrollbar: boolean = true;

    /**
     * Displays a close button if set
     */
    @Prop()
    has_close_button: boolean = true;

    /**
     * If sidepanel should use fixed positioning (will fallback to absolute)
     */
    @Prop({ reflect: true })
    position: SidePanelPosition = 'fixed';

    /**
     * Whether to show a backdrop behind the panel, prevents any interaction with background UI.
     */
    @Prop({ reflect: true })
    backdrop: boolean = false;

    /**
     * Will close the sidepanel if clicked
     */
    @Prop({ reflect: true })
    close_backdrop: boolean = false;

    /**
     * Target an external element to use as the trigger. When provided, clicking an element wia matching data-sidepanel attribute value will toggle the side panel.
     */
    @Prop() trigger_id?: string;

    @State()
    isExpanded = false;

    @State() isOpen: boolean = false;

    /**
     * Emits an event when the side panel is toggled, with `event.detail` being true if the panel is now open
     */
    @Event()
    atuiSidepanelChange: EventEmitter;

    private sidePanelWrapper: HTMLElement;
    private panelDialog: HTMLDialogElement;
    private triggerEls: HTMLElement[] = [];
    private externalTriggerListeners: Array<{
        element: HTMLElement;
        event: string;
        handler: EventListener;
    }> = [];

    /**
     * Toggles the side panel between open and closed states
     * @returns Promise that resolves when the panel state is toggled
     */
    @Method()
    async toggleSidePanel(): Promise<void> {
        if (this.isExpanded) {
            await this.closeSidePanel();
        } else {
            await this.openSidePanel();
        }
    }

    /**
     * Opens the side panel
     * @returns Promise that resolves when the panel is opened
     */
    @Method()
    async openSidePanel(): Promise<void> {
        if (this.panelDialog && !this.panelDialog.open) {
            if (this.backdrop === true) {
                this.panelDialog.showModal();
            } else {
                this.panelDialog.show();
            }

            if (this.backdrop) {
                this.panelDialog.classList.add('backdrop');
            }

            // Use requestAnimationFrame to delay the state change and apply css
            requestAnimationFrame(() => {
                this.isExpanded = true;
                this.isOpen = true;
                this.atuiSidepanelChange.emit(this.isOpen);
            });
        }
    }

    /**
     * Closes the side panel
     * @returns Promise that resolves when the panel is closed
     */
    @Method()
    async closeSidePanel(): Promise<void> {
        if (this.panelDialog && this.panelDialog.open) {
            this.panelDialog.close();
            this.isExpanded = false;
            this.isOpen = false;
            this.atuiSidepanelChange.emit(this.isOpen);
            this.panelDialog.classList.remove('backdrop');
        }
    }

    /**
     * Getter method for the open state of the side panel
     * @returns The current open state of the side panel
     */
    @Method()
    async getIsOpen(): Promise<boolean> {
        return this.isOpen;
    }

    private handleClose = () => {
        this.closeSidePanel();
    };

    private handleDialogClose = (event: Event) => {
        event.preventDefault();
        if (this.isExpanded) {
            this.closeSidePanel();
        }
    };

    private handleKeyDown = (event: KeyboardEvent) => {
        if (event.key === 'Escape' && this.isExpanded) {
            event.preventDefault();
            this.closeSidePanel();
        }
    };

    @Listen('mousedown', { target: 'document' })
    offClickHandler(event: any) {
        if (!this.close_backdrop || !this.panelDialog?.open) return;
        if (!this.sidePanelWrapper?.contains(event.target)) {
            this.handleClose();
        }
    }

    async componentDidLoad() {
        if (this.trigger_id) {
            this.triggerEls = Array.from(
                document.querySelectorAll(
                    `[data-sidepanel="${this.trigger_id}"]`,
                ),
            ) as HTMLElement[];

            if (this.triggerEls.length === 0) {
                console.warn(
                    `at-side-panel: No elements found with data-sidepanel="${this.trigger_id}"`,
                );
                return;
            }

            this.setupExternalTriggerListeners();
        }
    }

    disconnectedCallback() {
        this.cleanupExternalTriggerListeners();
    }

    private cleanupExternalTriggerListeners() {
        this.externalTriggerListeners.forEach(({ element, event, handler }) => {
            element.removeEventListener(event, handler);
        });
        this.externalTriggerListeners = [];
    }

    private setupExternalTriggerListeners() {
        if (!this.triggerEls || this.triggerEls.length === 0) return;

        const clickHandler = async (event: Event) => {
            event.preventDefault();
            event.stopPropagation();
            await this.toggleSidePanel();
        };

        const keydownHandler = async (event: KeyboardEvent) => {
            switch (event.key) {
                case 'Enter':
                case ' ':
                    event.preventDefault();
                    await this.toggleSidePanel();
                    break;
            }
        };

        this.triggerEls.forEach((el) => {
            el.addEventListener('click', clickHandler);
            el.addEventListener('keydown', keydownHandler);
            this.externalTriggerListeners.push(
                { element: el, event: 'click', handler: clickHandler },
                { element: el, event: 'keydown', handler: keydownHandler },
            );
        });
    }

    render() {
        return (
            <Host data-open={this.isOpen}>
                <dialog
                    ref={(el) => (this.panelDialog = el)}
                    class={`${this.backdrop ? 'backdrop' : ''}`}
                    onClose={this.handleDialogClose}
                    onKeyDown={this.handleKeyDown}
                >
                    <div
                        data-scrollable={this.has_scrollbar}
                        data-open={this.isOpen}
                        class={`container origin-${this.origin} size-${this.size} size-${this.size} position-${this.position}`}
                        ref={(el) => (this.sidePanelWrapper = el)}
                        data-name="container"
                    >
                        <header
                            class="header"
                            data-name="header"
                        >
                            <div>
                                {this.panel_title && (
                                    <h3 class="title">{this.panel_title}</h3>
                                )}
                                {this.panel_subtitle && (
                                    <p class="subtitle">
                                        {this.panel_subtitle}
                                    </p>
                                )}
                            </div>

                            {this.has_close_button && (
                                <at-button
                                    size="md"
                                    icon="close"
                                    type="secondaryText"
                                    data-name="panel-close"
                                    onClick={this.handleClose}
                                />
                            )}
                        </header>

                        <div
                            data-name="content"
                            class="content"
                        >
                            <slot />
                        </div>
                    </div>
                </dialog>
            </Host>
        );
    }
}
