@use '../../styles/shared/backdrop';

:host {
    --token-color-white-rgb: 255, 255, 255;
    display: contents;

    // Panel container
    .container {
        position: fixed;
        display: flex;
        flex-direction: column;
        z-index: var(--token-z-index-nav);
        min-width: var(--token-width-panel-xs);
        background-color: var(--token-color-white);
        box-shadow: var(--token-shadow-md);
        overflow-x: hidden;
        overflow-y: auto;
        height: 100%;
        opacity: 0;
        transition: all 300ms ease;
        will-change: transform, opacity;

        // Width
        &.width-xs {
            width: var(--token-width-panel-xs);
        }
        &.width-sm {
            width: var(--token-width-panel-sm);
        }
        &.width-md {
            width: var(--token-width-panel-md);
        }
        &.width-lg {
            width: var(--token-width-panel-lg);
        }
        &.width-xl {
            width: var(--token-width-panel-xl);
        }

        // Origin
        &.origin-left {
            left: 0;
            top: 0;
            transform: translateX(-100%);
        }
        &.origin-right {
            right: 0;
            top: 0;
            transform: translateX(100%);
        }

        // Position
        &.position-absolute {
            position: absolute;
        }

        // Scroll Overflow override
        &:not([data-scrollable]) {
            overflow-y: hidden;
        }

        // Animate on open
        &[data-open] {
            opacity: 1;
            visibility: visible;
            transform: translateX(0);
        }
    }

    .header {
        z-index: var(--token-z-index-nav);
        position: sticky;
        top: 0;
        padding: 12px 8px 12px 16px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        background-color: rgba(var(--token-color-white-rgb), 0.8);
        backdrop-filter: blur(10px);

        div {
            display: flex;
            flex-direction: column;
            gap: 2px;

            .title {
                font-size: var(--token-font-size-h4);
                font-weight: var(--token-font-weight-med);
                color: var(--token-text-dark);
                line-height: 1;
            }

            .subtitle {
                font-size: var(--token-font-size-sm);
                color: var(--token-text-light);
                line-height: 1;
            }
        }
    }

    .content {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        width: 100%;
    }
}
